/* eslint-disable no-console */
/* eslint-disable no-unused-vars */
/* eslint-disable arrow-body-style */
import React, {useState,useCallback} from 'react';
import ActorGrid from '../components/actor/ActorGrid';
import CustomRadio from '../components/CustomRadio';
import MainPageLayout from '../components/MainPageLayout';
import ShowGrid from '../components/show/ShowGrid';
import {apiGet} from '../misc/config';
import {useLastQuery} from '../misc/custom-hooks';
import { RadioInputsWrapper, SearchButtonWrapper, SearchInput } from './Home.styled';

const renderResults=(results)=>{

    if(results && results.length===0){

        return <div>No Results</div>;
        
    }
    if(results && results.length >0){

        return results[0].show ? <ShowGrid  data={results} /> : <ActorGrid  data={results} />;


    }

    return null;
}

const Home = () => {

    const [input, setInput]= useLastQuery();
    const [results, setResults]=useState(null);
    const [searchOption, setSearchOption]=useState('shows');

    const isShowsSearch=searchOption=== 'shows';

    

    const onInputchange=useCallback(ev=>{

        setInput(ev.target.value);

    },[setInput]);

    const onRadionChange=useCallback(ev=>{

        setSearchOption(ev.target.value);
        
    },[]) ;

  

    const onSearch =() =>{

        apiGet(`/search/${searchOption}?q=${input}`).then(
            result =>{
 // eslint-disable-next-line no-console
 setResults(result);
        }
        );

      
    }

    const onKeyDown =(ev) =>{

        if(ev.keyCode===13){
            onSearch();
        }

    }



  
    return (
        <MainPageLayout>
           <SearchInput type="text" placeholder="Search For Something" onChange={onInputchange} onKeyDown={onKeyDown} value={input} />
           <RadioInputsWrapper>
               <div>
<CustomRadio label="Shows" id="shows-search" value="shows" checked={isShowsSearch} onChange={onRadionChange} />
              
             </div>
               <div>
               <CustomRadio label="Actors" id="actors-search" value="people" checked={!isShowsSearch} onChange={onRadionChange} />
     </div>
               </RadioInputsWrapper>
           <SearchButtonWrapper><button type="button" onClick={onSearch}>Search</button></SearchButtonWrapper>
           {renderResults(results)}
        </MainPageLayout>
    )
}

export default Home
